.gradient-box {
  display: flex;
  align-items: center;
  margin: auto;
  position: relative;
  padding: 7px 15px;
  box-sizing: border-box;
  background: #000;
  background-clip: padding-box;
  border: solid 1px transparent;
  border-radius: 8px;
  transition: all .4s;
  z-index: 0;
}
.gradient-box:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -2;
  margin: -1px;
  border-radius: inherit;
  background: linear-gradient(to right,#322eed,#e82bd5);
}
.gradient-box:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  border-radius: 8px;
  z-index: -1;
}

